import { AuthService } from './../../SERVICES/auth.service';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';

const routes: Routes = [
  {
    path: '',
    component: TabsPage,
    children: [
      {
        path: 'tab1',
        loadChildren: () => import('../asso-map-home/asso-map-home.module').then( m => m.AssoMapHomePageModule),
        canActivate: [AuthService]
      },
      {
        path: 'tab2',
        loadChildren: () => import('../asso-list/tab2.module').then(m => m.Tab2PageModule),
        canActivate: [AuthService]
      },
      {
        path: 'tab3',
        loadChildren: () => import('../asso-post/tab3.module').then(m => m.Tab3PageModule),
        canActivate: [AuthService]
      },
      {
        path: 'tab4',
        loadChildren: () => import('../asso-profile/tab4.module').then(m => m.Tab4PageModule),
        canActivate: [AuthService]
      },
      {
        path: 'tab5',
        loadChildren: () => import('../assomyevents/assomyevents.module').then( m => m.AssomyeventsPageModule),
        canActivate: [AuthService]
      },
      {
        // dynamic
        path: 'post/:titre',
        loadChildren: () => import('../post/post.module').then( m => m.PostPageModule)
      }
     
    ]
  },
  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TabsPageRoutingModule {}
